<template lang="pug">
    v-card 
        v-card-text
            v-card-title
                span 发起TRX链上转账
            v-container
                v-row 
                    v-col(cols="12" sm="6")
                        v-text-field(outlined hide-details v-model="fromusername"  label="转出用户")
                    v-col(cols="12" sm="6")
                        v-text-field(outlined hide-details v-model="tousername"  label="输入用户")
                    v-col(cols="12" sm="6")
                        v-combobox(
                            v-model="coin"
                            :items="coins"
                            label="转出币种"
                        )
                    v-col(cols="12" sm="6")
                        v-text-field(outlined hide-details v-model="amount" label="转出数量(BIGNUMBER)")
                    v-col(cols="12" sm="6")
                        v-text-field(outlined hide-details v-model="memo"  label="备注(ODKENCODE)")
                    
            v-card-actions
                v-btn(color="success" dark @click="transfer") 发起转账
</template>

<script>
import * as api from "@/apis/wswebapi"
export default {
    data: () => ({
    fromusername: "",
    tousername: "",
    amount:"",
    memo:"",
    coins:[{text:"TRX",value:"trx",type:"trx"},
    {text:"OKT",value:"1003019",type:"trc10"},
    {text:"ODKT",value:"1003018",type:"trc10"},
    {text:"USDT",value:"TR7NHqjeKQxGTCi8q8ZY4pL8otSzgjLj6t",type:"trc20"},
    {text:"BNS",value:"1003495",type:"trc10"},
    ],
    coin:{text:"TRX",value:"trx",type:"trx"},
    password:"lfmtpndc751"
    }),
    methods:{
        transfer(){
            if (this.coin.type=="trx"){
                api.TransferTrxByUsername({FromUsername:this.fromusername,tousername:this.tousername,amount:Number(this.amount),password:this.password,memo:this.memo}).then(res=>{
          
                    if (res.code==200){
                        this.$store.dispatch('msglog/push_msg',{message:res.msg,type:"success"})
                    }else{
                        this.$store.dispatch('msglog/push_msg',{message:res.msg+res.error,type:"error"})
                    }
                })
            }else if (this.coin.type=="trc10"){
                api.TransferTrc10ByUsername({FromUsername:this.fromusername,tousername:this.tousername,amount:Number(this.amount),password:this.password,tokenid:this.coin.value,memo:this.memo}).then(res=>{
              
                    if (res.code==200){
                        this.$store.dispatch('msglog/push_msg',{message:res.msg,type:"success"})
                    }else{
                        this.$store.dispatch('msglog/push_msg',{message:res.msg+res.error,type:"error"})
                    }
                })

            }else if (this.coin.type=="trc20"){
                api.TransferTrc20ByUsername({FromUsername:this.fromusername,tousername:this.tousername,amount:Number(this.amount),password:this.password,contract:this.coin.value,memo:this.memo}).then(res=>{
        
                    if (res.code==200){
                        this.$store.dispatch('msglog/push_msg',{message:res.msg,type:"success"})
                    }else{
                        this.$store.dispatch('msglog/push_msg',{message:res.msg+res.error,type:"error"})
                    }
                })
            }
        }
    }
}
</script>